.App {
  text-align: center;
  justify-content: center;
  display: flex;
  background: linear-gradient(0deg, #f8f5f5);
width: 80.67%;
height: 100%;
}
.button{
  background-color: black;
  color: #f8f5f5;
  margin-top: 2rem;
  width: 200px;
  height: 40px;
  border-radius: 5px  ;
}
.forms{
  margin-top: 10rem;
  font-size: larger;
}
input{
  width: 250px;
  height: 30px;
  padding: 5px;
  border-radius: 5px;
}

.thanks{
  margin-top: 13rem;
  font-size: larger;
}

.Side{
  width:43.33%;
height: 100%;
background:linear-gradient(
  120deg,black,rgb(104, 8, 104),rgb(142, 177, 241),rgb(4, 4, 163),purple,black
);
margin-right: 35rem;
height: 650px;

}
.front{
  width: 430px;
  height: 240px;
background:linear-gradient(145deg,rgb(3, 3, 250),rgb(0, 0, 207),rgb(145, 145, 255),rgb(164, 34, 240),rgb(3, 35, 94));
margin-top: 5rem;
margin-left: 10rem;
border: 1px;
box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
border-radius: 18px  ;
}
.round{
  display: flex;

}
.round1{
  border: 2px solid white;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  color:white;
  margin-left: 3rem;
  margin-top: 2rem;
  background-color: #f8f5f5;
}
.round2{
 border: 1.5px solid white;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  color: rgb(117, 103, 247);
  margin-left: 2rem;
  margin-top: 2.5rem;
 
}
.number{
  margin-top: 3rem;
  color: #f8f5f5;
  font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
font-size: 20px;
text-align:center;
letter-spacing: normal;

}
.name{
  margin-top: 4rem;
 text-align: left;
 margin-left: 2rem;
}
.date{
  margin-top: -2.5rem;
  text-align: right;
  margin-right: 2rem;
}

.back{
  width: 430px;
height: 240px;
background-color: rgb(149, 148, 151);
box-shadow: 0px 8px 8px 0px rgba(0, 0, 0, 0.25);
margin-top: 2rem;
margin-left: 15rem;
border-radius: 18px  ;
}

.line{
  width: 430px;
  height: 40px;
  background-color: black;
}

.line1{
  width: 350px;
  height: 40px;
  background-color: rgb(104, 93, 93);
  margin-top: 2rem;
  border-radius: 12px  ;
  margin-left: 2.5rem;
  text-align: right;
  font-size: larger;
}

.underline{
  width: 200px;
  height: 5px;
  background-color: rgb(104, 93, 93);
  margin-top: 3rem;
  margin-left: 6rem;
  border-radius: 5px;
}
.underline1{
  width: 150px;
  height: 5px;
  background-color: rgb(104, 93, 93);
  margin-top: 1rem;
  margin-left: 8rem;
  border-radius: 5px;
}
.thanks{
 text-align: center;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}






@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
